<template>
  <v-row class="px-4">
    <v-col
      cols="12"
      class="text-center text-h4"
    >
      Авторизация
    </v-col>

    <v-col class="d-flex justify-center">
      <v-card
        flat
        color="transparent"
        width="400"
      >
        <v-row>
          <v-col
            cols="12"
            class="pb-2"
          >
            <v-form>
              <v-text-field
                v-model="username"
                outlined
                hide-details="auto"
                label="Логин"
                class="rounded-lg mb-5"
              />
              <v-text-field
                v-model="password"
                outlined
                :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                :type="show ? 'text' : 'password'"
                hide-details="auto"
                label="Пароль"
                class="rounded-lg mb-5"
                @click:append="show = !show"
              />
            </v-form>
          </v-col>

          <v-col>
            <ApolloMutation
              :mutation="require('../graphql/mutations/adminLogin.gql')"
              :variables="{
                username,
                password,
              }"
              @done="onLogin"
            >
              <template v-slot="{ mutate, loading, error }">
                <v-btn
                  :disabled="!username || !password"
                  :loading="loading"
                  block
                  depressed
                  color="primary"
                  class="text-none rounded-lg"
                  x-large
                  @click="mutate()"
                >
                  Войти
                </v-btn>
                <AppNotification
                  v-if="error"
                  :message="error.message"
                  :icon-disabled="true"
                />
              </template>
            </ApolloMutation>
          </v-col>
        </v-row>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import AppNotification from '@/components/AppNotification.vue';

export default {
  components: {
    AppNotification,
  },
  data() {
    return {
      username: '',
      password: '',
      show: false,
    };
  },
  methods: {
    onLogin({ data }) {
      localStorage.setItem('token', data.adminLogin.accessToken);
      this.$router.push('/');
    },
  },
};
</script>
